import React, { useContext, useState } from 'react';
import * as _ from 'lodash';
import Parcours from '../parcours/Parcours';
import { ApiContext } from '../../contexts/ApiContext';
import '../../App.scss';
import './ParcoursList.scss';
import MapParcours from '../map/MapParcours';
import CurrentCourseContext from '../../contexts/CurrentCourseContext';
import CurrentParcoursContext from '../../contexts/CurrentParcoursContext';

const ParcoursList = () => {
  const { course, coord } = useContext(ApiContext);
  const parcours = [...course];
  const coordList = [...coord];
  const [currentCourse, setCurrentCourse] = useState(1);
  const [currentParcoursMap, setCurrentParcoursMap] = useState(['50.6379','3.05166']);

  const uniqueCourseByID = _.uniqBy(parcours, 'course_id');

  const contextValue = {
    currentCourse,
    updateMap: setCurrentCourse,
  };

  const contextCurrentMap = {
    currentParcoursMap,
    updateParcoursMap: setCurrentParcoursMap,
  };

  return (
    <CurrentCourseContext.Provider value={contextValue}>
      <CurrentParcoursContext.Provider value={contextCurrentMap}>
        <div className="content">
          <div className="parcours">
            <div className="mapContainer ">
              <MapParcours />
            </div>
            <div className="parcoursWrap">
              <div className="parcoursInfo">
                {parcours && (
                  <div className="parcoursCapsule">
                    {uniqueCourseByID.map((parc) => (
                      <Parcours
                        key={parc.id}
                        parcours={parc}
                        fullparcours={parcours}
                      />
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </CurrentParcoursContext.Provider>
    </CurrentCourseContext.Provider>
  );
};

export default ParcoursList;
