import React, { useContext } from 'react';
import { Map, TileLayer } from 'react-leaflet';
import { LocationContext } from '../../contexts/LocationContext';
import MarkerParcours from '../marker/MarkerParcours';
import 'leaflet/dist/leaflet.css';
import CurrentParcoursContext from '../../contexts/CurrentParcoursContext';

const MapParcours = () => {
  const value = useContext(CurrentParcoursContext);
  console.log(value.currentParcoursMap);

  return (
    <>
      <Map
        id="map"
        center={value.currentParcoursMap} // La map se centre d'office sur les coordonnées passées en param
        zoom={14}
        style={{ height: '100%', zIndex: 0 }}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.fr/osmfr/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        <MarkerParcours />
      </Map>
    </>
  );
};
export default MapParcours;
